body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(124, 186, 236);
}

.header{
  background-color:rgb(68, 160, 152);
  height: 50px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
h1{
  color: rgb(46, 42, 42);
  font-style: italic;
}
.note {
  color: grey;
  padding-top: 3px;
  font-size: 15px;
}

button{
  padding-right: 5px;
  padding-left: 5px;
  margin-left: 10px;
}
.error{
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px

  
}